import { initializeDynamicCampaignList } from "../../donate/listCampaigns";
import { initializeDynamicProductList } from "../../donate/listCampaignProducts";
import { WFComponent, WFFormComponent } from "@xatom/core";
import { WFSlider } from "@xatom/slider";
import { userAuth } from "../../../auth/authConfig";
import {
  toggleError,
  setupValidation,
  createValidationFunction,
} from "../../../utils/formUtils";
import { validateEmail } from "../../../utils/validationUtils";
import { apiClient } from "../../../api/apiConfig"; // Assuming you have an apiClient to handle requests

// Define the response type for the begin_checkout endpoint
export interface BeginCheckoutResponse {
  checkout_url: string;
}

// Load the donation state from localStorage
const loadState = () => {
  const savedState = localStorage.getItem("donationState");
  if (savedState) {
    return JSON.parse(savedState);
  }
  return {};
};

// Save the donation state to localStorage
const saveState = (state: any) => {
  localStorage.setItem("donationState", JSON.stringify(state));
};

// Set sidebar first name from userAuth
const firstNameText = new WFComponent("#firstNameText");
const user = userAuth.getUser();

if (user && user.profile && user.profile.first_name) {
  firstNameText.setText(user.profile.first_name);
} else {
  firstNameText.setText("friend");
}

// Hide email input if user is authenticated
const emailWrap = new WFComponent("#emailWrap");
const firstNameInput = new WFComponent("#firstNameInput");
const firstNameInputError = new WFComponent("#firstNameInputError");
const lastNameInput = new WFComponent("#lastNameInput");
const lastNameInputError = new WFComponent("#lastNameInputError");
const emailInput = new WFComponent("#emailInput");
const emailInputError = new WFComponent("#emailInputError");

const validateNotEmpty = (value: string) => value.trim() !== "";

if (user && user.profile && user.email) {
  emailWrap.setStyle({ display: "none" });
} else {
  setupValidation(
    firstNameInput,
    firstNameInputError,
    createValidationFunction(
      firstNameInput,
      validateNotEmpty,
      "First name is required."
    )
  );
  setupValidation(
    lastNameInput,
    lastNameInputError,
    createValidationFunction(
      lastNameInput,
      validateNotEmpty,
      "Last name is required."
    )
  );
  setupValidation(
    emailInput,
    emailInputError,
    createValidationFunction(
      emailInput,
      validateEmail,
      "Please enter a valid email address."
    )
  );
}

const slider = new WFSlider(".multi-step_form_slider");

// Initialize sidebar components
const sidebarStepOne = new WFComponent("#sidebarStepOne");
const sidebarStepTwo = new WFComponent("#sidebarStepTwo");

const sidebarSteps = [sidebarStepOne, sidebarStepTwo];

const updateSidebar = (currentStepIndex: number) => {
  console.log(`Updating sidebar to step ${currentStepIndex}`);
  sidebarSteps.forEach((step, index) => {
    if (!step) return; // Skip if the step is not defined
    if (index === currentStepIndex) {
      step.addCssClass("is-active");
    } else {
      step.removeCssClass("is-active");
    }
  });
};

const triggerSuccessAnimation = (stepIndex: number) => {
  console.log(`Triggering success animation for step ${stepIndex}`);
  if (sidebarSteps[stepIndex]) {
    sidebarSteps[stepIndex].getElement().click();
  } else {
    console.warn(`No sidebar step defined for step index ${stepIndex}`);
  }
};

slider.onSlideChange((activeIndex, prevIndex) => {
  console.log(`Slide changed from ${prevIndex} to ${activeIndex}`);
  updateSidebar(activeIndex);

  if (prevIndex !== -1 && prevIndex < activeIndex) {
    triggerSuccessAnimation(prevIndex);
  }

  if (prevIndex !== -1 && prevIndex > activeIndex) {
    triggerSuccessAnimation(activeIndex);
  }
});

updateSidebar(slider.getActiveSlideIndex());

let isCampaignListInitialized = false;

// Function to navigate to a specific step based on URL parameters
const navigateToStepFromURL = async () => {
  const params = new URLSearchParams(window.location.search);
  const campaignId = params.get("campaign");
  const checkoutCanceled = params.has("checkoutcanceled");
  const loadingWall = new WFComponent(".loading_wall");

  if (checkoutCanceled) {
    loadingWall.setStyle({ display: "flex" });
    const checkoutSessionId = localStorage.getItem("checkoutSessionId");
    if (checkoutSessionId) {
      try {
        await apiClient
          .post("/donate/cancel_checkout_session", {
            data: { checkoutSessionId },
          })
          .fetch();
        localStorage.removeItem("checkoutSessionId");
      } catch (error) {
        console.error("Error canceling checkout session:", error);
      }
    }
  }

  if (campaignId) {
    const state = loadState();
    state.selectedCampaignId = campaignId;
    saveState(state);
    loadingWall.setStyle({ display: "flex" });

    try {
      // Initialize the product list for the provided campaign ID
      await initializeDynamicProductList("#selectProductList", campaignId);

      // Set the slider to slide 2
      slider.goToIndex(1);
      updateSidebar(1);

      // Fade out the loading wall
      loadingWall.setStyle({ opacity: "0" });
      setTimeout(() => {
        loadingWall.setStyle({ display: "none" });
      }, 500); // Duration should match the CSS transition duration
    } catch (error) {
      console.error("Error initializing product list for campaign:", error);
    }
  } else {
    // Initialize the campaign list if no campaignId is provided
    await initializeDynamicCampaignList("#selectCampaignList");
    isCampaignListInitialized = true;

    // Fade out the loading wall
    loadingWall.setStyle({ opacity: "0" });
    setTimeout(() => {
      loadingWall.setStyle({ display: "none" });
    }, 500); // Duration should match the CSS transition duration
  }
};

export const makeDonation = async () => {
  const formStepOne = new WFFormComponent<{ campaign_id: string }>(
    "#formStepOne"
  );
  const formStepTwo = new WFFormComponent<{
    product_id: string;
    is_anonymous: boolean;
    first_name: string;
    last_name: string;
    email: string;
  }>("#formStepTwo");

  const stepOneRequestingAnimation = new WFComponent(
    "#stepOneRequestingAnimation"
  );
  const submitStepOneError = new WFComponent("#submitStepOneError");

  await navigateToStepFromURL();

  formStepOne.onFormSubmit(async (formData, event) => {
    event.preventDefault();
    stepOneRequestingAnimation.setStyle({ display: "block" });

    const { selectedCampaignId } = loadState();

    if (!selectedCampaignId) {
      const errorMessage = "Please select a campaign.";
      toggleError(submitStepOneError, errorMessage, true);
      stepOneRequestingAnimation.setStyle({ display: "none" });
      return;
    }

    console.log("Selected Campaign ID:", selectedCampaignId);

    try {
      // Initialize the product list for the selected campaign
      await initializeDynamicProductList(
        "#selectProductList",
        selectedCampaignId
      );

      stepOneRequestingAnimation.setStyle({ display: "none" });
      slider.goNext(); // Transition to the next step using the slider
    } catch (error) {
      console.error("Error during campaign selection:", error);
      stepOneRequestingAnimation.setStyle({ display: "none" });
      toggleError(
        submitStepOneError,
        "Failed to load products for the selected campaign. Please try again.",
        true
      );
    }
  });

  const stepTwoRequestingAnimation = new WFComponent(
    "#stepTwoRequestingAnimation"
  );
  const submitStepTwoError = new WFComponent("#submitStepTwoError");

  formStepTwo.onFormSubmit(async (formData, event) => {
    event.preventDefault();
    stepTwoRequestingAnimation.setStyle({ display: "block" });

    const { selectedProductId } = loadState();
    const { is_anonymous, first_name, last_name, email } = formData;

    if (!selectedProductId) {
      const errorMessage = "Please select a product.";
      toggleError(submitStepTwoError, errorMessage, true);
      stepTwoRequestingAnimation.setStyle({ display: "none" });
      return;
    }

    if (!user && (!first_name || !last_name || !validateEmail(email))) {
      const errorMessage = "Please fill in all required fields.";
      if (!first_name)
        toggleError(firstNameInputError, "First name is required.", true);
      if (!last_name)
        toggleError(lastNameInputError, "Last name is required.", true);
      if (!validateEmail(email))
        toggleError(
          emailInputError,
          "Please enter a valid email address.",
          true
        );
      stepTwoRequestingAnimation.setStyle({ display: "none" });
      return;
    }

    console.log("Selected Product ID:", selectedProductId);

    try {
      const state = loadState();
      state.selectedProductId = selectedProductId;
      state.is_anonymous = is_anonymous;
      state.is_user = !!user; // Add is_user to the state

      if (user && user.profile && user.email) {
        state.email = user.email;
        state.first_name = user.profile.first_name;
        state.last_name = user.profile.last_name;
      } else {
        state.email = email;
        state.first_name = first_name;
        state.last_name = last_name;
      }
      saveState(state);

      // Send a POST request with the state to /donate/begin_checkout
      const response = await apiClient
        .post<BeginCheckoutResponse>("/donate/begin_checkout", {
          data: state,
        })
        .fetch();

      if (response && response.checkout_url) {
        const checkoutUrl = new URL(response.checkout_url);
        const checkoutSessionId = checkoutUrl.pathname.split("/pay/")[1];
        localStorage.setItem("checkoutSessionId", checkoutSessionId); // Save the session ID to localStorage
        window.location.href = response.checkout_url; // Redirect to the Stripe checkout URL
      }

      stepTwoRequestingAnimation.setStyle({ display: "none" });
    } catch (error) {
      console.error("Error during product selection:", error);
      stepTwoRequestingAnimation.setStyle({ display: "none" });
      toggleError(
        submitStepTwoError,
        "Failed to select product. Please try again.",
        true
      );
    }
  });

  // Handle the "Go Back" button click
  const backStepTwoButton = document.getElementById("backStepTwo");
  if (backStepTwoButton) {
    backStepTwoButton.addEventListener("click", async () => {
      slider.goToIndex(0); // Navigate back to step one
      updateSidebar(0);

      if (!isCampaignListInitialized) {
        await initializeDynamicCampaignList("#selectCampaignList");
        isCampaignListInitialized = true;
      }
    });
  }
};
